.header {
    position: fixed;
    width: 100%;
    height: 2.8rem;
    background-color: black;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    z-index: 10;
    white-space: nowrap;
}

.welcome {
    border-width: 5px;
    border-color: #0070f3;
    border-radius: 10px;
    margin: 0.7rem;
    padding: 1rem;
    font-weight: 700;
    font-size: 2.5rem;
    animation: blinker 1s step-start infinite;
}

@keyframes blinker {
    50% {
        opacity: 0;
    }
}

.card {
    margin: 0.7rem;
    padding: 0.8rem;
    text-align: center;
    color: inherit;
    text-decoration: none;
    border: 1px solid #eaeaea;
    border-radius: 10px;
    transition: color 0.15s ease, border-color 0.15s ease;
    max-width: 900px;
}

.card:hover,
.card:focus,
.card:active {

    border-width: 5px;
    border-color: #0070f3;
}

.headerlist {
    display: flex;
}

.headerlist li {
    padding: 0 1rem;
}

.headerlist li a {
    font-size: 1.2rem;
    font-weight: 500;
}

.hamburger {
    display: none;
}

@media screen and (max-width: 1040px) {

    .headerlist {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 50%;
        right: -100%;
        z-index: -3;
        transition: 0.4s;
    }

    .headerlistActive {
        right: 0;
        width: 50%;
        height: 100%;
        position: fixed;
        margin-right: 5%;
        z-index: 1;
        top: 0;
        right: 100;
        background-color: #111;
        overflow-x: hidden;
        opacity: 0.9;
        padding: 30px;
    }

    .headerlist li {
        padding: 1rem 0;
    }

    .headerlist li a {
        font-size: 1.2rem;

    }

    .hamburger {
        display: initial;
    }
}